import React, { useState, useEffect } from 'react';
import './LifeGraphHome.css';
import {
  Container,
  Box,
  AppBar,
  Toolbar,
  Typography,
  Button,
  Card,
  CardContent,
  IconButton,
  Stack,
  useTheme
} from '@mui/material';
import {
  Home as HomeIcon,
  Save as SaveIcon,
  Upload as UploadIcon,
  Settings as SettingsIcon,
  AutoAwesome as AutoAwesomeIcon,
  Edit as EditIcon,
  Sync as SyncIcon
} from '@mui/icons-material';
import EventInputForm from './EventInputForm';
import GraphDisplay from './GraphDisplay';

function LifeGraphHome() {
  const [currentView, setCurrentView] = useState('home');
  const [events, setEvents] = useState([]);
  const [loadFont, setLoadFont] = useState(false);
  const [loadedFiles, setLoadedFiles] = useState([]);
  const [graphDatasets, setGraphDatasets] = useState([]);

  // 폰트 로딩 확인을 위한 useEffect
  useEffect(() => {
    // 나눔고딕 폰트 로딩
    const font = new FontFace('NanumGothic', 'url(https://fonts.gstatic.com/ea/nanumgothic/v5/NanumGothic-Regular.woff2) format("woff2")');
    
    font.load().then(() => {
      // 폰트 로딩 완료시 문서에 적용
      document.fonts.add(font);
      setLoadFont(true);
    }).catch(err => {
      console.error('폰트 로딩 실패:', err);
      // 폰트 로딩 실패해도 앱은 계속 진행
      setLoadFont(true);
    });
  }, []);

  // 수동 입력 화면으로 이동
  const navigateToManualInput = () => {
    setCurrentView('manualInput');
  };

  // 통합 화면으로 이동 (입력과 그래프 같이 보기)
  const navigateToIntegratedView = () => {
    setCurrentView('integratedView');
  };

  // 그래프 화면으로 이동 (이벤트 데이터 전달)
  const navigateToGraphView = (eventData) => {
    setEvents(eventData);
    setCurrentView('graphView');
  };

  // 홈 화면으로 돌아가기
  const navigateToHome = () => {
    setCurrentView('home');
  };

  // 이벤트 추가 함수
  const handleAddEvent = (newEvent) => {
    setEvents([...events, newEvent]);
  };

  // 이벤트 목록 업데이트 함수
  const handleUpdateEvents = (updatedEvents) => {
    setEvents(updatedEvents);
  };

  // 이벤트 저장하기 함수
  const handleSaveEvents = () => {
    try {
      const userName = localStorage.getItem('userName') || '사용자';
      const now = new Date();
      const dateStr = now.toISOString().slice(0, 10).replace(/-/g, '');
      const timeStr = now.toTimeString().slice(0, 8).replace(/:/g, '');
      
      // 이벤트 데이터를 JSON 문자열로 변환
      const jsonData = JSON.stringify(events, null, 2);
      
      // Blob 객체 생성
      const blob = new Blob([jsonData], { type: 'application/json' });
      
      // 다운로드 링크 생성
      const url = URL.createObjectURL(blob);
      const a = document.createElement('a');
      a.href = url;
      a.download = `${userName}_인생그래프_${dateStr}_${timeStr}.json`;
      
      // 클릭 이벤트 트리거
      document.body.appendChild(a);
      a.click();
      
      // 정리
      setTimeout(() => {
        document.body.removeChild(a);
        URL.revokeObjectURL(url);
      }, 100);
      
      alert('인생그래프가 저장되었습니다.');
    } catch (error) {
      console.error('저장 중 오류 발생:', error);
      alert('저장 중 오류가 발생했습니다.');
    }
  };

  // 이벤트 불러오기 함수
  const handleLoadEvents = (e) => {
    const file = e.target.files[0];
    if (!file) return;

    const reader = new FileReader();
    reader.onload = (event) => {
      try {
        const loadedEvents = JSON.parse(event.target.result);
        const fileName = file.name.split('_')[0];
        
        // 이름 세팅 (기존 이름이 없을 때만)
        if (!localStorage.getItem('userName')) {
          localStorage.setItem('userName', fileName);
        }

        // 데이터셋 추가
        const newDataset = {
          label: `${fileName}님의 인생그래프`,
          data: loadedEvents,
          borderColor: '#4e54c8',
          borderWidth: 2,
          tension: 0.4,
          pointRadius: 6,
          pointHoverRadius: 8
        };
        
        setGraphDatasets([...graphDatasets, newDataset]);
        setEvents(loadedEvents);
        
        alert('인생그래프를 성공적으로 불러왔습니다.');
        // 통합 화면으로 전환
        setCurrentView('integratedView');
      } catch (error) {
        console.error('파일 읽기 오류:', error);
        alert('파일 형식이 올바르지 않습니다.');
      }
    };
    reader.readAsText(file);
  };

  // 이벤트 처리 함수 - 이벤트 발생시 진동 효과 (모바일)
  const handleButtonClick = (callback) => {
    // 진동 API가 있는지 확인하고 진동 효과 추가
    if (navigator.vibrate) {
      navigator.vibrate(50); // 50ms 진동
    }
    
    // 콜백 실행
    callback();
  };

  // 통합 화면 컴포넌트
  const IntegratedView = () => {
    return (
      <Box sx={{ height: '100vh', display: 'flex', flexDirection: 'column' }}>
        <AppBar position="static" color="primary" elevation={0}>
          <Toolbar>
            <Typography variant="h6" component="h1" sx={{ flexGrow: 1 }}>
              인생그래프
            </Typography>
            <Button
              startIcon={<HomeIcon />}
              color="inherit"
              onClick={() => handleButtonClick(navigateToHome)}
            >
              홈으로
            </Button>
            <Button
              startIcon={<SaveIcon />}
              color="inherit"
              onClick={() => handleButtonClick(handleSaveEvents)}
              disabled={events.length === 0}
            >
              저장하기
            </Button>
            <Button
              component="label"
              startIcon={<UploadIcon />}
              color="inherit"
            >
              불러오기
              <input
                type="file"
                hidden
                accept=".json"
                onChange={handleLoadEvents}
              />
            </Button>
          </Toolbar>
        </AppBar>

        <Box
          sx={{
            display: 'flex',
            flex: 1,
            gap: 2,
            p: 2,
            width: '90%',
            margin: '0 auto'
          }}
        >
          <Card sx={{ flex: 1 }}>
            <CardContent>
              <EventInputForm
                events={events}
                onAddEvent={handleAddEvent}
                onUpdateEvents={handleUpdateEvents}
                isEmbedded={true}
              />
            </CardContent>
          </Card>
          <Card sx={{ flex: 3 }}>
            <CardContent>
              <GraphDisplay
                events={events}
                isEmbedded={true}
              />
            </CardContent>
          </Card>
        </Box>
      </Box>
    );
  };

  // 현재 보여줄 화면 결정
  if (!loadFont) {
    // 폰트 로딩 중 표시
    return (
      <Box
        sx={{
          height: '100vh',
          display: 'flex',
          alignItems: 'center',
          justifyContent: 'center'
        }}
      >
        <Typography variant="h5">로딩 중...</Typography>
      </Box>
    );
  }
  
  if (currentView === 'manualInput') {
    return <EventInputForm 
      onBack={navigateToHome} 
      onSubmit={navigateToGraphView}
      events={events}
      onUpdateEvents={handleUpdateEvents}
    />;
  }
  
  if (currentView === 'graphView') {
    return <GraphDisplay 
      events={events} 
      onBack={() => handleButtonClick(() => setCurrentView('manualInput'))} 
    />;
  }

  if (currentView === 'integratedView') {
    return <IntegratedView />;
  }

  return (
    <Container maxWidth="md" sx={{ py: 4 }}>
      <Box sx={{ mb: 4, display: 'flex', justifyContent: 'space-between', alignItems: 'center' }}>
        <Box sx={{ display: 'flex', alignItems: 'center', gap: 2 }}>
          <img src="/logo.png" alt="인생그래프 로고" style={{ height: 60 }} />
          <Typography variant="h4" component="h1">
            인생그래프
          </Typography>
        </Box>
        <IconButton
          onClick={() => handleButtonClick(() => alert('설정 메뉴는 개발 중입니다.'))}
          size="large"
        >
          <SettingsIcon />
        </IconButton>
      </Box>

      <Stack spacing={2}>
        <Button
          variant="contained"
          size="large"
          startIcon={<AutoAwesomeIcon />}
          onClick={() => handleButtonClick(() => alert('AI 자동 생성 기능은 곧 제공될 예정입니다.'))}
          sx={{ py: 2 }}
        >
          자동으로 만들어줘요
        </Button>
        
        <Button
          variant="contained"
          size="large"
          startIcon={<EditIcon />}
          onClick={() => handleButtonClick(navigateToManualInput)}
          sx={{ py: 2 }}
        >
          내가 직접 써볼래요
        </Button>
        
        <Button
          variant="contained"
          size="large"
          startIcon={<SyncIcon />}
          onClick={() => handleButtonClick(navigateToIntegratedView)}
          sx={{ py: 2 }}
        >
          한 화면에서 작업하기
        </Button>
      </Stack>

      <Box sx={{ mt: 4, textAlign: 'center' }}>
        <Typography variant="subtitle1" color="text.secondary">
          💬 한 줄 설명: "내 이야기를 그래프로 그려보는 시간"
        </Typography>
      </Box>
    </Container>
  );
}

export default LifeGraphHome; 